<template>
  <div class="wrapper">

    <header class="header">
      <div class="container">
        <div class="searchBlock">
          <img src="../assets/logo.png" class="logo" alt="HaveFun">
          <div class="searchBlock__search">
            <div class="searchIcon">
              <font-awesome-icon :icon="['fas', 'search']" />
            </div>
            <input type="text"
              class="searchInput"
              v-model="searchText"
              @keyup="updateFilter"
              placeholder="Explore your own activities">
          </div>
        </div>
      </div>
    </header>

    <main class="main">
      <div class="container">
        <section class="filters">
          <div class="filter">
            <div class="filter__header">
              <span class="subtitle">地區</span>
              <div class="headerIcon">
                <font-awesome-icon :icon="['fas', 'plus']" />
              </div>
            </div>
            <div class="filter__body">
              <el-select
                v-model="selectLocation"
                clearable
                @change="updateFilter"
                placeholder="請選擇地區">

                <el-option
                  v-for="item in zoneList"
                  :key="item.value"
                  :label="item.label"
                  :value="item.value">
                </el-option>

              </el-select>
            </div>
          </div>

          <div class="filter">
            <div class="filter__header">
              <span class="subtitle">分類</span>
              <div class="headerIcon">
                <font-awesome-icon :icon="['fas', 'plus']" />
              </div>
            </div>
            <div class="filter__body">
              <el-checkbox-group v-model="selectCategories">
                <el-checkbox
                  v-for="item in categories"
                  :label="item.filterName"
                  :key="item.filterValue"
                  @change="updateFilter">
                {{item.filterValue}}
                </el-checkbox>
              </el-checkbox-group>

            </div>
          </div>

          <div class="filter" v-if="false">
            <div class="filter__header">
              <span class="subtitle">最後更新日期</span>
              <div class="headerIcon">
                <font-awesome-icon :icon="['fas', 'plus']" />
              </div>
            </div>
            <div class="filter__body">
              <div class="dateBlock">
                <span class="demonstration">from</span>
                <el-date-picker
                  v-model="selectDateStart"
                  type="date"
                  class="dateSelect"
                  placeholder="选择日期">
                </el-date-picker>
              </div>
              <div class="dateBlock">
                <span class="demonstration">to</span>
                <el-date-picker
                  v-model="selectDateEnd"
                  type="date"
                  class="dateSelect"
                  placeholder="选择日期">
                </el-date-picker>
              </div>
            </div>
          </div>
        </section>

        <section class="content">
          <div class="resultInfo hidden-mobile">
            <h3>
              Showing <span class="resultInfo--count">{{filterCount}}</span> results by…
            </h3>

            <span class="resultInfo--tag"
              v-for="(item, index) in getTag" :key="index"
              @click="cleanFilterCondition(item)"
              >
              {{item.tagValue}}
              <font-awesome-icon :icon="['far', 'times-circle']" />
            </span>
          </div>

          <transition-group name="list" tag="ol" class="resultList" mode="in-out">
            <li is="card"
              @f_showMap="initMap"
              :item="item"
              class="card"
              v-for="item in currPageDatas"
              :key="item.Id">
            </li>
          </transition-group>

          <el-pagination
            background
            :current-page.sync="currentPage"
            layout="prev, pager, next"
            :total="filterCount"
            :pager-count="5"
            v-if="filterCount">
          </el-pagination>
        </section>
      </div>
    </main>


    <transition name="fade">

      <div class="map__block" v-show="show" >
        <div class="map__block__info">
          <h2 class="title">
            <font-awesome-icon :icon="['fas', 'map-marker-alt']" />
            {{ mapInfo.Name }}
          </h2>
          <span class="add"> {{ mapInfo.Add }}</span>
          <a href="javascript:;" class="bt-closeMap" @click="closeMap">
            ×
          </a>
        </div>

        <div id="map" class="map__block__location"></div>
      </div>

    </transition>

  </div>
</template>

<script>
import FontAwesomeIcon from '@fortawesome/vue-fontawesome';
import card from './card';

export default {
  name: 'index',
  components: {
    FontAwesomeIcon,
    card
  },
  data() {
    return {
      // 篩選後資料
      filterCount: 0,
      filterDatas: [],
      // 分頁資料
      currentPage: 0,
      pageDatas: [],
      // 過濾條件
      searchText: '',
      selectLocation: '',
      selectDateStart: '',
      selectDateEnd: '',
      selectCategories: [],
      // 全部地區
      zoneList: [],
      // 分類
      categories: [
        {
          filterName: 'Opentime',
          filterValue: '全天候開放'
        },
        {
          filterName: 'Ticketinfo',
          filterValue: '免費參觀'
        }
      ],
      show: false,
      mapInfo: ''
    };
  },
  mounted() {
    const apiUrl = 'https://data.kcg.gov.tw/api/action/datastore_search';
    /* eslint-disable */

    const params = {
      resource_id: '92290ee5-6e61-456f-80c0-249eae2fcc97',
      limit: 999
    };

    this.$http
      .get(apiUrl, {
        params: params
      })
      .then(response => {
        /* eslint-disable no-console */
        // console.log(response.data);

        this.filterDatas = response.data.result.records || {};
        this.filterCount = response.data.result.total || 0;

        this.computedPageDatas();

        // 篩選出 地區
        const zoneArr = response.data.result.records.map(item => item.Zone);
        const zoneArrUnique = [...new Set(zoneArr)];

        this.zoneList = zoneArrUnique.map(item => {
          return {
            label: item,
            value: item
          };
        });
        /* eslint-enable no-console */

        var uluru = { lat: 22.63961, lng: 120.30211 };
        var map = new google.maps.Map(document.getElementById('map'), {
          zoom: 16,
          center: uluru
        });
        var marker = new google.maps.Marker({
          position: uluru,
          map: map
        });
      });
  },
  methods: {
    updateFilter() {
      // 過濾條件----------------------------------------Start
      const filterCondition = {};

      // 地區
      if (this.selectLocation) filterCondition.Zone = this.selectLocation;

      // 分類
      this.categories.map(item => {
        if (this.selectCategories.find(value => item.filterName === value))
          filterCondition[item.filterName] = item.filterValue;
      });

      /* eslint-disable no-console */
      // console.log('filterCondition', filterCondition);
      /* eslint-enable no-console */
      // 過濾條件-------------------------------------------End

      // API+過濾條件
      const apiUrl = 'https://data.kcg.gov.tw/api/action/datastore_search';
      const params = {
        resource_id: '92290ee5-6e61-456f-80c0-249eae2fcc97',
        limit: 999,
        q: this.searchText,
        filters: JSON.stringify(filterCondition)
      };

      this.$http
        .get(apiUrl, {
          params: params
        })
        .then(response => {
          /* eslint-disable no-console */
          // console.log(response.data);
          this.filterDatas = response.data.result.records || {};
          this.filterCount = response.data.result.total || 0;
          this.computedPageDatas();
          /* eslint-enable no-console */
        });
    },
    cleanFilterCondition(obj) {
      /* eslint-disable no-console */

      // 清除地區
      if (obj.tagName === 'Zone') this.selectLocation = '';

      // 清除分類
      var newArr = this.selectCategories.filter(function(value) {
        return value !== obj.tagName;
      });

      this.selectCategories = newArr;
      this.updateFilter();
    },
    computedPageDatas() {
      const sourceData = this.filterDatas;
      const newData = [];
      const count = 10; // 每頁儲放幾筆

      sourceData.forEach((item, i) => {
        // 每 n 筆後，產生新頁
        if (i % count === 0) {
          newData.push([]);
        }

        // 計算資料屬於第幾頁
        const page = parseInt(i / count);
        newData[page].push(item);
      });

      this.pageDatas = newData;
    },
    initMap(data) {
      this.show = true;
      this.mapInfo = JSON.parse(JSON.stringify(data));

      const uluru = { lat: +data.Py, lng: +data.Px };
      const map = new google.maps.Map(document.getElementById('map'), {
        zoom: 16,
        center: uluru
      });
      const marker = new google.maps.Marker({
        position: uluru,
        map: map
      });
    },
    closeMap() {
      this.show = false;
      this.mapInfo = '';
    }
  },
  computed: {
    getTag() {
      const tag = [];
      // 地區
      if (this.selectLocation) {
        tag.push({
          tagName: 'Zone',
          tagValue: this.selectLocation
        });
      }

      // 分類
      this.categories.map(item => {
        if (this.selectCategories.find(value => item.filterName === value))
          tag.push({
            tagName: item.filterName,
            tagValue: item.filterValue
          });
      });

      // console.log('tag', tag);

      return tag;
    },
    currPageDatas() {
      return this.pageDatas[this.currentPage - 1];
    }
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lan="scss">
</style>



